import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { HashLocationStrategy, LocationStrategy } from '@angular/common';

import { AppComponent } from './app.component';
import { VideoComponent } from './an-video/an-video.component';
import { BiddingComponent } from './header-bidding/header-bidding.component';
import { PrebidComponent } from './prebid-script/prebid-script.component';
import { FullComponent } from './full-width/full-width.component';
import { XboxComponent } from './xbox-tracker/xbox-tracker.component';
import { NativeComponent } from './native-gallery/native-gallery.component';
import { NativeEComponent } from './native-editor/native-editor.component';
import { ClassTrackerComponent } from './class-tracker/class-tracker.component';

import { AppRoutingModule } from './app-routing.module';

@NgModule({
  declarations: [
    AppComponent,
    VideoComponent,
    BiddingComponent,
    PrebidComponent,
    FullComponent,
    XboxComponent,
    NativeComponent,
    NativeEComponent,
    ClassTrackerComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    FormsModule,
    HttpModule,
    NgbModule.forRoot()
  ],
  providers: [{provide: LocationStrategy, useClass: HashLocationStrategy}],
  bootstrap: [AppComponent]
})
export class AppModule { }



