import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { VideoComponent } from './an-video/an-video.component';
import { BiddingComponent } from './header-bidding/header-bidding.component';
import { PrebidComponent } from './prebid-script/prebid-script.component';
import { FullComponent } from './full-width/full-width.component';
import { XboxComponent } from './xbox-tracker/xbox-tracker.component';
import { NativeComponent } from './native-gallery/native-gallery.component';
import { NativeEComponent } from './native-editor/native-editor.component';
import { ClassTrackerComponent } from './class-tracker/class-tracker.component';

const routes: Routes = [
  { path: '', redirectTo: '/an-video', pathMatch: 'full' },
  { path: 'an-video', component: VideoComponent },
  { path: 'header-bidding', component: BiddingComponent },
  { path: 'prebid-script', component: PrebidComponent },
  { path: 'full-width', component: FullComponent },
  { path: 'xbox-tracker', component: XboxComponent },
  { path: 'native-gallery', component: NativeComponent },
  { path: 'native-editor', component: NativeEComponent },
  { path: 'class-tracker', component: ClassTrackerComponent }
]

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})

export class AppRoutingModule {}



